export const FontApp = (props) => {
  const fontNodes = props.data.map((data) => (
    <div className="col">
      <div className="card h-100">
        <div className={data.css}>
          <div className="card-header">{data.name}</div>
          <div className="card-body">
            <h2 className="card-title">{data.type}</h2>
            <input
              rows="6"
              cols="50"
              className={data.css}
              type="text"
              name="fontTest"
              placeholder=""
            />
            <p className="card-text">
              sample
            </p>
            <p className="card-text">{data.description_JP}</p>
            <p className="card-text">{data.description}</p>
            {/* {data.weight}<br />
                {data.weight.length} */}
          </div>
        </div>
      </div>
    </div>
  ));
  return (
    <div className="container px-0">
      <div className="row row-cols-1 row-cols-md-2 g-4">{fontNodes}</div>
    </div>
  );
};

export default FontApp;
